.partners {
  padding: 1rem 0 2.5rem;
}
@media screen and (width <= 768px) {
  .partners {
    padding-bottom: 0;
  }
}
.partners__breadcrumbs {
  margin-bottom: 5rem;
}
@media screen and (width <= 768px) {
  .partners__breadcrumbs {
    margin-bottom: 2.5rem;
  }
}
.partners__subtitle {
  margin-bottom: 4rem;
}
@media screen and (width <= 768px) {
  .partners__subtitle {
    margin-bottom: 1.5rem;
  }
}
.partners__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.5rem;
}
@media screen and (width <= 1024px) {
  .partners__list {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (width <= 990px) {
  .partners__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (width <= 768px) {
  .partners__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.25rem;
  }
}
.partners__item {
  background-color: var(--light-color);
  border-radius: 1rem;
  padding: 2rem;
  min-height: 194px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.partners__item:hover .partners__item-info {
  opacity: 1;
}
@media screen and (width <= 768px) {
  .partners__item {
    height: 90px;
    min-height: auto;
  }
}
.partners__item-info {
  background-color: var(--color-red);
  border-radius: 1rem;
  padding: 1.5rem;
  color: var(--light-color);
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (width <= 768px) {
  .partners__item-info {
    padding: 1rem;
  }
}
.partners__item-text {
  font-family: var(--font-pt);
  font-size: 0.875rem;
  text-align: center;
}
@media screen and (width <= 768px) {
  .partners__item-text {
    display: none;
  }
}
.partners__item-link {
  margin-top: auto;
  text-align: center;
  text-decoration: underline;
}
@media screen and (width <= 768px) {
  .partners__img {
    height: 65px;
    object-fit: contain;
  }
}